/**
 * [日历组件]
 ------------------调用示例(配置说明)-----------------------------------
 // 创建 日历
var date = new DatePicker({
      id:"date",                  // 日历组件 dom class（必须否则无法 创建 由于用的是jQuery选择器 id请+ # class + .）
      maxDate:'2016/8/31',        // 可选 时间段最大时间（非必选，注意格式一定要相同）
      minDate:'2016/8/1'          // 可选 时间段最小时间（非必选，注意格式一定要相同）
      isTimeBucket:true            // 设置 是否启用时间段 （ true | false）
  });
 */

/*  ----------------接口说明----------------------------------
// 设置日历时间 （注意：设置参数一定要和下面一样）
date.setAndGetDate().setDate({
       year:2016,
        month:8,
        day:1
     });
 // 获取日历时间
 date.setAndGetDate().getDate();      // yyyy-mm-dd
  // 获取用户选择状态
date.setAndGetDate().getIsSelectDate()   // false or true
date.setTimeBucket().setIsTimeBucket(true);           // 设置 是否启用时间段 （ true | false）
date.setTimeBucket().setMinTimeBucket(3);             // 设置 时间段最小跨度 (大于0整数)
date.setTimeBucket().setMaxTimeBucket(5);             // 设置 时间段最大跨度 (大于0整数)
 */


var DatePicker = function (options, jquery) {
        // 创建日历组件的 页面元素 id
        if (!options.id) return;
        this.nodeid = options.id;
        this.td     = []; // 存储 dom 的td 对象
        // 初始化时日历当前时间
        this.year   = new Date().getFullYear();   // 当前时间 年
        this.month  = new Date().getMonth() + 1;  // 当前时间 月
        this.day    = new Date().getDate();       // 当前时间 日

        this.isSelectDate = false; // 用户现在状态
        // 是否启用时间段

        if (options.isTimeBucket && typeof options.isTimeBucket === 'boolean') {
            this.isTimeBucket = options.isTimeBucket;
        } else {
            this.isTimeBucket = false;
        }

        var regularVal = /^[1-9]\d*$/; // 非0正整数
        //  时间段最小跨度
        this.minTimeBucket = regularVal.test(options.minTimeBucket) ?
                                                      options.minTimeBucket : 2;
        //  时间段最大跨度
        this.maxTimeBucket = regularVal.test(options.maxTimeBucket) ?
                                                    options.maxTimeBucket : 60;
        this.selectDate = { //保存当前选择 日期(时间选择时 第一次选择时间)
            year  : new Date().getFullYear(),
            month : new Date().getMonth() + 1,
            day   : new Date().getDate()
        };
        this.selectDate2 = { //时间选择时 保存第二次选择时间
            year  : new Date().getFullYear(),
            month : new Date().getMonth() + 1,
            day   : new Date().getDate()
        };
        // 可选日期范围  最大日期
        if (options.maxDate && typeof options.maxDate === 'string') {
            this.maxDate = new Date(options.maxDate);
        } else {
            // 默认 为空
            this.maxDate = null;
        }
        // 可选日期范围  最大日期
        if (options.minDate && typeof options.minDate === 'string') {
            this.minDate = new Date(options.minDate);
        } else {
            // 默认 为空
            this.minDate = null;
        }
        this._$ = jquery;
        this.init();
    }
    DatePicker.prototype = {
        init: function() {
            this.createDom();
            this.bindEvents();
            this.render();
        },
        createDom: function() {
            // 创建dom对象或者创建html片段或者创建template
            //  添加 年和月
            //  添加日历输入框
            var $ = this._$;
            $(this.nodeid).append("<div class='date-input'>" +
                                  "<i class='date-iconfont'>&#xe600;</i>" +
                                  "<input type='text' name='date-input'></div>");
            //  添加日历dom
            $(this.nodeid).append("<div class='date'></div>");
            // 初始化时隐藏日历组件
            $(this.nodeid + ' .date').css({"display": "none"});
            $(this.nodeid + ' .date').append("<div class='head'>" +
                                              "<div class='year'><i class='year-left'><</i>" +
                                              "<span class='year-data'>年</span><i class='year-right'>></i></div> " +
                                              "<div class='month'><i class='month-left'><</i>" +
                                              "<span class='month-data'>月</span><i class='month-right'>></i></div></div>");
            $(this.nodeid + ' .date').append("<table></table>");
            //  添加星期
            $(this.nodeid + ' .date table').append("<tr><th>日</th><th>一</th><th>二</th><th>三</th><th>四</th><th>五</th><th>六</th></tr>");
            // 添加 天的包裹元素 td
            for (var i = 0; i < 6; i++) {
                var tr = document.createElement('tr');
                $(this.nodeid + ' .date table').append($(tr));
                for (var j = 0; j < 7; j++) {
                    var td = document.createElement('td');
                    tr.appendChild(td);
                    this.td.push(td);
                }
            }
        },
        bindEvents: function() {
            //事件绑定
            var $ = this._$;
            var that = this;
            //  年减少
            $(this.nodeid + ' .date .head .year-left').click(function() {
                if (that.year > 0) {
                    that.year--;
                    that.render();
                }
            });
            //  年增加
            $(this.nodeid + ' .date .head .year-right').click(function() {
                that.year++;
                that.render();
            });
            //  月减少
            $(this.nodeid + ' .date .head .month-left').click(function() {
                if (that.month > 1) {
                    that.month--;
                } else {
                    that.year--;
                    that.month = 12;
                }
                that.render();
            });
            //  月增加
            $(this.nodeid + ' .date .head .month-right').click(function() {
                if (that.month < 12) {
                    that.month++;
                } else {
                    that.year++;
                    that.month = 1;
                }
                that.render();
            });
            //  点击 td 选择 日期
            var isStateClick = true; // 点击状态 (二次点击)
            $(this.nodeid + ' .date table td').click(function() {
                if (!this.firstChild) return;
                if (this.firstChild.getAttribute('class') === 'clickable') {
                    //  判断是否启动时间段
                    if (that.isTimeBucket) {
                        var tempChar = that.year + '/' +
                                       that.month + '/' +
                                       this.firstChild.innerHTML;
                        var selectDateChar = that.selectDate.year + '/' +
                                             that.selectDate.month + '/' +
                                             that.selectDate.day;
                        var selectDateChar2 = that.selectDate2.year + '/' +
                                              that.selectDate2.month + '/' +
                                              that.selectDate2.day;
                        if (isStateClick) { // 第一次选择
                            if (tempChar !== selectDateChar && tempChar !== selectDateChar2) {
                                var isisTimeSpanVal = that.isTimeSpan(selectDateChar,
                                                                      tempChar,
                                                            that.minTimeBucket,
                                                            that.maxTimeBucket);
                                if (!isisTimeSpanVal) {
                                    that.selectDate2.year  = that.year;
                                    that.selectDate2.month = that.month;
                                    that.selectDate2.day   = parseInt(this.firstChild.innerHTML);
                                    that.render();
                                    isStateClick = !isStateClick;
                                } else {
                                    alert(isisTimeSpanVal);
                                }
                            }
                        } else { // 第二次选择
                            if (tempChar !== selectDateChar && tempChar !== selectDateChar2) {
                                // 第二次选择清空之前的选择
                                that.selectDate = {
                                    year: null,
                                    month: null,
                                    day: null
                                };
                                that.selectDate2 = {
                                    year: null,
                                    month: null,
                                    day: null
                                };
                                $(that.nodeid + ' .date-input input').get(0).value = '';
                                that.selectDate.year  = that.year;
                                that.selectDate.month = that.month;
                                that.selectDate.day   = parseInt(this.firstChild.innerHTML);
                                that.render();
                            }
                            isStateClick = !isStateClick;
                        }


                        if (that.selectDate2.year && that.selectDate.year) {
                            // 用户已经选择
                            that.isSelectDate = true;
                            var minVal = that.compareDate(that.selectDate, that.selectDate2).minDateChar;
                            var maxVal = that.compareDate(that.selectDate, that.selectDate2).maxDateChar;
                            $(that.nodeid + ' .date-input input').get(0).value = minVal + '~' + maxVal;
                        }

                    } else {
                        that.selectDate.year = that.year;
                        that.selectDate.month = that.month;
                        that.selectDate.day = parseInt(this.firstChild.innerHTML);
                        that.render();
                        // 隐藏日历
                        $(that.nodeid + ' .date').css({
                            "display": "none"
                        });
                        // 设置 输入框的值
                        $(that.nodeid + ' .date-input input').get(0).value = that.setAndGetDate().getDate();
                        // 用户已经选择
                        this.isSelectDate = true;
                    }

                } else if (this.firstChild.getAttribute('class') === 'unclickable') {
                    alert('该日期不可选');
                }

            });
            // 日历图标 点击显示日历
            // var stateClick = true;   // 点击状态 用于隐藏切换
            $(this.nodeid + ' .date-input .date-iconfont').click(function() {
                $(that.nodeid + ' .date').css({
                    "display": "block"
                });

            });
            // 点击输入框 显示日历
            $(this.nodeid + ' .date-input input').click(function() {
                $(that.nodeid + ' .date').css({
                    "display": "block"
                });
            });

            // 点击页面 获取页面元素
            $(document).mouseup(function(e){
              var _con = $(that.nodeid);   // 设置目标区域
              if(!_con.is(e.target) && _con.has(e.target).length === 0){
                $(that.nodeid + ' .date').css({
                    "display": "none"
                });
              }
            });
        },
        render: function() {
            var $ = this._$;
            //渲染函数，更新数据或样式
            var nowDate = this.calculate(this.year, this.month, this.day);
            // 添加 年
            $(this.nodeid + ' .date .head .year-data').get(0).textContent = this.year + '年' ;
            // 添加 月
            $(this.nodeid + ' .date .head .month-data').get(0).textContent = this.month + '月';

            // 重置 td
            for (var j = 0; j < this.td.length; j++) {
                this.td[j].innerHTML = '';
                this.td[j].style.backgroundColor = '';
                this.td[j].style.color = '';
            }
            // 添加 这个月 天数
            for (var i = 0; i < nowDate.monthLen; i++) {
                // maxDate 为空
                if (this.minDate && !this.maxDate) {
                    if (new Date(this.year, this.month - 1, i + 1) >= this.minDate) {
                        this.td[i + nowDate.whichDay].innerHTML = "<span class='clickable'>" + (i + 1) + "</span>";
                    } else {
                        // 不可选时间样式
                        this.td[i + nowDate.whichDay].innerHTML = "<span class='unclickable'>" + (i + 1) + "</span>";
                    }
                }
                // minDate 为空
                if (!this.minDate && this.maxDate) {
                    if (new Date(this.year, this.month - 1, i + 1) <= this.maxDate) {
                        this.td[i + nowDate.whichDay].innerHTML = "<span class='clickable'>" + (i + 1) + "</span>";
                    } else {
                        // 不可选时间样式
                        this.td[i + nowDate.whichDay].innerHTML = "<span class='unclickable'>" + (i + 1) + "</span>";
                    }
                }
                // minDate和maxDate 都不为空
                if (this.minDate && this.maxDate) {
                    if (new Date(this.year, this.month - 1, i + 1) <= this.maxDate &&
                        new Date(this.year, this.month - 1, i + 1) >= this.minDate) {
                        this.td[i + nowDate.whichDay].innerHTML = "<span class='clickable'>" + (i + 1) + "</span>";
                    } else {
                        this.td[i + nowDate.whichDay].innerHTML = "<span class='unclickable'>" + (i + 1) + "</span>";
                    }
                }
                // minDate和maxDate 都为空
                if (!this.minDate && !this.maxDate) {
                    this.td[i + nowDate.whichDay].innerHTML = "<span class='clickable'>" + (i + 1) + "</span>";
                }
            }
            // 添加上个月 天数
            for (var k = 0; k < nowDate.whichDay; k++) {
                this.td[nowDate.whichDay - k - 1].innerHTML = "<span class='elseMonth'>" +
                                                              (nowDate.lastMonthLen - k) + "</span>";
            }
            // 添加下个月 天数
            for (var g = nowDate.whichDay + nowDate.monthLen, num = 1; g < this.td.length; g++) {
                this.td[g].innerHTML = "<span class='elseMonth'>" + (num++) + "</span>";
            }
            // 添加 选择的天 显示效果
            if (this.year === this.selectDate.year && this.month === this.selectDate.month)
                if (this.selectDate.day) {
                    var selectDate = this.calculate(this.selectDate.year, this.selectDate.month, this.selectDate.day);
                    this.td[selectDate.whichDay + this.selectDate.day - 1].style.backgroundColor = 'rgb(240, 93, 89)';
                    this.td[selectDate.whichDay + this.selectDate.day - 1].style.color = 'rgb(226, 231, 250)';
                }
                // 启用时间段是 渲染第二次选择
            if (this.isTimeBucket) {
                if (this.year === this.selectDate2.year && this.month === this.selectDate2.month)
                    if (this.selectDate2.day) {
                        var selectDate2 = this.calculate(this.selectDate2.year, this.selectDate2.month, this.selectDate2.day);
                        this.td[selectDate2.whichDay + this.selectDate2.day - 1].style.backgroundColor = 'rgb(240, 93, 89)';
                        this.td[selectDate2.whichDay + this.selectDate2.day - 1].style.color = 'rgb(226, 231, 250)';
                    }
                    // 如果开始时间和结束时间都有值才渲染 时间段
                if (this.selectDate2.year && this.selectDate.year) {
                    this.addTimeBucketCss();
                }
            }

        },
        setAndGetDate: function() {
            // 设置 和获取 日期接口
            var that = this;
            return {
                setDate: function(dateVal) { // 设置日期
                    // maxDate 为空
                    if (that.minDate && !that.maxDate) {
                        if (new Date(dateVal.year, dateVal.month - 1, dateVal.day) >= that.minDate) {
                            that.selectDate.year = dateVal.year;
                            that.selectDate.month = dateVal.month;
                            that.selectDate.day = dateVal.day;
                            that.year = dateVal.year;
                            that.month = dateVal.month;
                            that.render();
                        } else {
                            alert('超过时间范围，无法设置');
                        }
                    }
                    // minDate 为空
                    if (!that.minDate && that.maxDate) {
                        if (new Date(dateVal.year, dateVal.month - 1, dateVal.day) <= that.maxDate) {
                            that.selectDate.year = dateVal.year;
                            that.selectDate.month = dateVal.month;
                            that.selectDate.day = dateVal.day;
                            that.year = dateVal.year;
                            that.month = dateVal.month;
                            that.render();
                        } else {
                            alert('超过时间范围，无法设置');
                        }
                    }
                    // minDate和maxDate 都不为空
                    if (that.minDate && that.maxDate) {
                        if (new Date(dateVal.year, dateVal.month - 1, dateVal.day) <= that.maxDate &&
                            new Date(dateVal.year, dateVal.month - 1, dateVal.day) >= that.minDate) {
                            that.selectDate.year = dateVal.year;
                            that.selectDate.month = dateVal.month;
                            that.selectDate.day = dateVal.day;
                            that.year = dateVal.year;
                            that.month = dateVal.month;
                            that.render();
                        } else {
                            alert('超过时间范围，无法设置');
                        }
                    }
                    // minDate和maxDate 都为空
                    if (!that.minDate && !that.maxDate) {
                        that.selectDate.year = dateVal.year;
                        that.selectDate.month = dateVal.month;
                        that.selectDate.day = dateVal.day;
                        that.year = dateVal.year;
                        that.month = dateVal.month;
                        that.render();
                    }
                },
                getDate: function() { // 获取日期
                    var dateVal = that.selectDate.year + '/' + that.selectDate.month + '/' + that.selectDate.day;
                    return dateVal;
                },
                getIsSelectDate: function() { // 获取用户选择状态
                    return that.isSelectDate;
                }
            };
        },
        addTimeBucketCss: function() {
            var $ = this._$;
            var min = this.compareDate(this.selectDate, this.selectDate2).minDate;
            var max = this.compareDate(this.selectDate, this.selectDate2).maxDate;
            var tdNode = $(this.nodeid + ' .date table .clickable');
            for (var i = 0; i < tdNode.length; i++) {
                var tdNodeDate = new Date(this.year, this.month - 1, parseInt(tdNode[i].innerHTML));
                if (tdNodeDate > min && tdNodeDate < max) {
                    tdNode[i].style.backgroundColor = 'rgba(71, 166, 227, 0.61)';
                    tdNode[i].style.color = 'rgb(226, 231, 250)';
                }
            }
        },
        setTimeBucket: function() {
            // 设置时间段 接口
            var that = this;
            var regularVal = /^[1-9]\d*$/; // 非0正整数
            return {
                setIsTimeBucket: function(newIsTimeBucket) {
                    // 设置是否启用时间段
                    if (newIsTimeBucket && typeof newIsTimeBucket === 'boolean') {
                        that.isTimeBucket = newIsTimeBucket;
                    } else {
                        that.isTimeBucket = false;
                    }
                },
                setMinTimeBucket: function(newMinTimeBucket) {
                    //  时间段最小跨度
                    that.minTimeBucket = regularVal.test(newMinTimeBucket) ? newMinTimeBucket : 2;
                },
                setMaxTimeBucket: function(newMaxTimeBucket) {
                    //  时间段最大跨度
                    that.maxTimeBucket = regularVal.test(newMaxTimeBucket) ? newMaxTimeBucket : 60;
                },
            };
        },
        setMinAdnMaxDate: function (){
            var that = this
            return {
                min: function (val) {
                    if (val && typeof val === 'string') {
                        that.minDate = new Date(val);
                        that.render();
                    }
                },
                max: function (val) {
                        if (val && typeof val === 'string') {
                        that.maxDate = new Date(val);
                        that.render();
                    }
                }
            }
        },
/**
 * [日期计算]
 * @param  {[string]} year  [年]
 * @param  {[string]} month [月]
 * @param  {[string]} day   [日]
 * @return {[object]}       [message]
 * message={
 * lastMonthLen   // 上个月有几天
 * monthLen       // 这个月有几天
 * nextMonthLen   // 下个月有几天
 * whichDay       // 1号是周几
 * }
 */

        calculate: function (year, month, day) {
            var date = year + '/' + month + '/' + '1';
            var whichDay = new Date(date).getDay();
            var message = {
                lastMonthLen: new Date(year, month - 1, 0).getDate(),
                monthLen: new Date(year, month, 0).getDate(),
                nextMonthLen: new Date(year, month + 1, 0).getDate(),
                whichDay: whichDay,
            };
            return message;
        },
/**
 * 时间对象比较函数
 */
        compareDate: function (d1, d2) {
            var temp1 = new Date(d1.year, d1.month - 1, d1.day);
            var temp2 = new Date(d2.year, d2.month - 1, d2.day);
            var max, min;
            if (temp1 > temp2) {
                max = temp1;
                min = temp2;
            } else {
                max = temp2;
                min = temp1;
            }
            return {
                minDate: min,
                maxDate: max,
                minDateChar: min.getFullYear() + '/' + (min.getMonth() + 1) + '/' + min.getDate(),
                maxDateChar: max.getFullYear() + '/' + (max.getMonth() + 1) + '/' + max.getDate()
            };

        },
        /**
         * 判断 时间选择是否 符合跨度要求
         */
        isTimeSpan: function (v1, v2, min, max) {
            var d1 = new Date(v1),
                d2 = new Date(v2);
            var minTimeSpan = (min - 2) * 86400000;
            var maxTimeSpan = max * 86400000;
            if (Math.abs(d1 - d2) <= minTimeSpan) {
                return '时间区间最小不要小于' + min + '天';
            } else if (Math.abs(d1 - d2) >= maxTimeSpan) {
                return '时间区间最大不要大于' + max + '天';
            } else {
                return false;
            }
        }

};

module.exports = DatePicker;



