<template lang="html">
  <div class="center">
    <ui-button type="flat" raised color="primary" @click="showModel()">点击显示模态框</ui-button>
    <div id="myModel"></div>
  </div>
</template>

<script>
import UiButton from 'keen-ui/lib/UiButton'
import Flyout from '../assets/lib/js/model.js'
export default {
  data() {
    return {
    };
  },
  computed: {},
  ready() {},
  attached() {},
  methods: {
    showModel: function(){
      new Flyout('标题', '这是一个模态框', 'myModel');
    }
  },
  components: {
    UiButton
  }
};
</script>

<style lang="css" scoped>
.center {
  display: flex;
  justify-content: center;
}
</style>



