<template>
  <div id="app">
    <my-head></my-head>
    <div class="content">
    	 <ui-collapsible header="一个基于jQuery的时间选择组件" class="outline-none" >
       <date-picker></date-picker>
      </ui-collapsible>
      <ui-collapsible header="一个原生模态框组件" class="outline-none" >
        <model></model>
     </ui-collapsible>
     <ui-collapsible header="简单的控制方块移动的小游戏" class="outline-none" >
       <div class="center">
         <div class="desc">
       <h3>说明：</h3>
       <p>通过输入指令来控制页面的方块的移动</p>
       <p>简单的实现了 A* 寻路算法 演示</p>
     </div>
     <ui-button type="flat" raised color="primary" @click="openDeom(36)">查看演示</ui-button>
   </div>
     </ui-collapsible>
     <ui-collapsible header="行星与飞船" class="outline-none" >
       <div class="center">
         <div class="desc">
           <h3>说明：</h3>
           <p>模拟飞船运动演示动画 </p>
           <p>体会JavaScript面向对象设计</p>
           <p>简单的实践JavaScript的基础设计模式</p>
         </div>
         <ui-button type="flat" raised color="primary" @click="openDeom(28)">查看演示</ui-button>
       </div>

     </ui-collapsible>
     <ui-collapsible header="几种遍历算法的演示" class="outline-none" >
       <div class="center">
         <ui-button type="flat" raised color="primary" @click="openDeom(23)">查看演示</ui-button>
       </div>
     </ui-collapsible>
     <ui-collapsible header="冒泡排序的简单可视化实现" class="outline-none" >
       <div class="center">
         <ui-button type="flat" raised color="primary" @click="openDeom(19)">查看演示</ui-button>
       </div>
     </ui-collapsible>
    </div>
  </div>
</template>

<script>
import MyHead from './components/head'
import UiCollapsible from 'keen-ui/lib/UiCollapsible'
import UiButton from 'keen-ui/lib/UiButton'
import datePicker from 'components/datePicker'
import model from 'components/model'
export default {
  components: {
    MyHead,
    UiCollapsible,
    datePicker,
    model,
    UiButton
  },
  methods: {
    openDeom: function(num){
      var host = window.location.host
      var herfVal = 'http://' + host + "/demo/task"+num+"/task"+num+".html"
      window.open( herfVal, '_blank')
    }
  }
}
</script>

<style >
.content {
	margin: 20px 10%;
}
.ui-collapsible-header{
	outline: none;
}
.center {
  display: flex;
  justify-content: space-around;;
  align-items: center;
}
.desc, .show-button{
  display: inline-block;
}
</style>



