<template>
	<div class="date-show">
		<h3>默认选择时间</h3>
		<div id="calendar1" class="date-center"></div>
	</div>
	<div class="date-show">
	<h3>选择日期范围</h3>
	<div id="calendar2" class="date-center"></div>
	</div>
	<div class="date-show">
	<h3>限制最小可选时间(-1)</h3>
	<div id="calendar3" class="date-center"></div>
	</div>
	<div class="date-show">
	<h3>限制最大可选时间(+5)</h3>
	<div id="calendar4" class="date-center"></div>
	</div>
</template>

<script>
import $ from 'jquery'
import DatePicker from '../assets/lib/js/datePicker'
export default {
  name: 'date-picker',
  data () {
    return {
    };
  },
  ready () {
		let _date1 = new DatePicker({id: '#calendar1'}, $)
		let _date2 = new DatePicker({id: '#calendar2', isTimeBucket: true}, $)
		let  now = new Date().getFullYear() + '/' + (new Date().getMonth() + 1)
		let minVal = now + '/' + new Date().getDate()
		let maxVal = now + '/' + (new Date().getDate() + 5)
		let _date3 = new DatePicker({id: '#calendar3', minDate: minVal}, $)
		let _date4 = new DatePicker({id: '#calendar4', maxDate: maxVal}, $)
  }
};
</script>

<style lang="css" scoped>
@import '../assets/lib/css/date-picker.css';
.date-center{
  display: inline-block;
}
.date-show{
	width: 49%;
	display: inline-block;
}
.date-show h3{
	display: inline-block;
	width: 30%;
	margin-left: 15%
}
</style>



